$font-main: 'Poppins', sans-serif;
$font-sec: "Montserrat", sans-serif;

.title {
  font-family: $font-sec;
  font-style: normal;
  font-weight: bold;
  letter-spacing: -0.02em;

  &-1 {
    font-size: 64px;
    line-height: 78px;
  }

  &-2 {
    font-size: 40px;
    line-height: 49px;
  }

  &-3 {
    font-size: 24px;
    line-height: 29px;
  }
}

.tx {
  &-body {
    font-family: $font-main;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
  }
}

.sbtitle {
  font-family: $font-main;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
}

.cl {
  &-primary {
    color: #FF5852;
  }

  &-second {
    color: #004369;
  }

  &-tertiary {
    color: #01949A;
  }

  &-tertiary {
    color: #01949A;
  }

  &-link {
    color: #4B4DED;
  }

  &-dark {
    color: #0A1B41;
  }

  &-success {
    color: #31D0AA;
  }

  &-error {
    color: #D03131;
  }

  &-text {
    color: #4A5268;
  }

  &-subtext {
    color: #787D8D;
  }
}

.bg {
  &-accent {
    color: #ECEFF4;
  }

  &-lihgt {
    color: #FAFBFE;
  }

  &-lihgt-accent {
    color: #FAFBFE;
  }

  &-white  {
    color: #FFFFFF;
  }
}